import axios from 'axios'
import { getWithAuth, postWithAuth, deleteWithAuth, patchWithAuth } from '../../../utils/requestHandler'

export const ElectionAPI = {
  async createElection(data) {
    const response = await postWithAuth("/api/elections/", data)
    return response
  },

  async getElection() {
    const response = await getWithAuth("/api/elections")
    return response
  },

  async getElectionSummary(unique_id) {
    const response = await getWithAuth(`/api/elections/${unique_id}/summary`)
    return response
  },

  async getElectionBy(unique_id) {
    const response = await getWithAuth(`/api/elections/${unique_id}`)
    return response
  },

  async patchElection(data,unique_id) {
    console.log(unique_id)
    const response = await patchWithAuth(`/api/elections/${unique_id}`,data)
    return response
  },

  async delElection(unique_id) {
    const response = await deleteWithAuth(`/api/elections/${unique_id}`)
    return response
  },
}