import React, { useEffect, useState } from 'react'
import { Link, useParams, useRouteMatch } from 'react-router-dom'
import { useMediaQuery } from 'react-responsive'
import Loading from '../../../utils/Loading'
import moment from 'moment'
import FormBallot from './FormBallot'
import FormBallotOption from './FormBallotOption'
import { BallotAPI } from '../api/BallotAPI'
import { ShowNotify, notifyPosition, notifyType } from '../../../utils/notification'


const noneListImg = require("../../../assets/img/icons/report.svg")

function ListBallot(props) {
  let { path, url } = useRouteMatch()
  const isTablet = useMediaQuery({ query: '(max-width: 725px)' })
  const [state, setState] = useState({
    loading: false,
    dataBallot: null,
    dataBallotOption: null,
    idBallot: '',
    idBallotOption: ''
  })
  const delBallot = async (unique_id, ballot_id) => {
    setState({ ...state, loading: true })
    const response = await BallotAPI.delBallot(unique_id, ballot_id)
    if (response) {
      if (response.status === 500) {
        ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
      } else if (response.status_code === 401) {
        ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
      } else if (response.status === 200) {
        ShowNotify("Succesfully delete ballot", notifyPosition.topCenter, notifyType.success, () => {
          window.location.reload()
        })
      }
    }
    setState({ ...state, loading: false })
  }
  const delBallotOption = async (unique_id, ballot_id, option_id) => {
    setState({ ...state, loading: true })
    const response = await BallotAPI.delBallotOption(unique_id, ballot_id, option_id)
    if (response) {
      if (response.status === 500) {
        ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
      } else if (response.status_code === 401) {
        ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
      } else if (response.status === 200) {
        ShowNotify("Succesfully delete ballot option!", notifyPosition.topCenter, notifyType.success, () => {
          window.location.reload()
        })
      }
    }
    setState({ ...state, loading: false })
  }
  useEffect(() => {
  }, [state.idBallot, state.idBallotOption, state.dataBallot, state.dataBallotOption])
  console.log(state.dataBallot)
  return (
    <div>
      <div className="list-election card card-body">
        <div className={props.listBallot && props.listBallot.length === 0 ? "d-none" : isTablet ? "d-block" : "d-flex"}>
          <div className="mr-auto">
            <button className="btn btn-success my-2 my-sm-0" data-toggle="modal" data-target="#addBallotModal">Tambah Jenis Pemilihan</button>
          </div>
          <form className={isTablet ? "mt-2 form-inline" : "ml-2 form-inline"}>
            <input className={isTablet ? "form-control w-100 mb-1" : "form-control mr-sm-2"} type="search" placeholder="Cari..." aria-label="Search" />
            <button className="btn btn-outline-success my-2 my-sm-0" type="submit">Cari</button>
          </form>
        </div>
        {props.listBallot && props.listBallot.length === 0 ? (
          <div className="d-flex align-items-center justify-content-center" style={{ height: "80vh" }}>
            <div className="text-center">
              <img src={noneListImg} width="100" height="100" />
              <br /><br /><p className="note-text">Klik tombol tambah jenis pemilihan dibawah ini!</p>
              <button className="btn btn-success my-2 my-sm-0" data-toggle="modal" data-target="#addBallotModal">Tambah Jenis Pemilihan</button>
            </div>
          </div>
        ) :
          (
            <div>
              {props.listBallot && props.listBallot.map((row, index) => (
                <div key={index} className="card mt-4">
                  <div className="modal fade" id={`putBallotModal${row.id}`} tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                      <div className="modal-content">
                        <div className="modal-header">
                          <h5 className="modal-title" id="exampleModalLabel">Edit Jenis Pemilihan</h5>
                          <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                          </button>
                        </div>
                        <div className="modal-body">
                          <FormBallot unique_id={url.split('election/').pop().split('/')[0]} dataBallot={row} ballot_id={row.id} mode="edit" />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="card-body">
                    <div className="card-title d-flex">
                      <span className="subtitle-text">
                        {row.title}
                      </span>
                      <div class="dropdown ml-auto show">
                        <button className="btn btn-sm order-1 order-lg-0 dropdown-toggle" id="dropdownBallotOption" data-toggle="dropdown"><i className="fa fa-ellipsis-h"></i></button>
                        <div class="dropdown-menu" aria-labelledby="dropdownBallotOption">
                          <a class="dropdown-item" href="#" data-toggle="modal" data-target={`#putBallotModal${row.id}`} onClick={e => setState({ ...state, idBallot: row.id, dataBallot: row })}>Edit</a>
                          <a class="dropdown-item" href="#" data-toggle="modal" data-target="#delBallotModal" onClick={e => setState({ ...state, idBallot: row.id })}>Hapus</a>
                        </div>
                      </div>
                    </div>
                    <div className="card-subtitle note-text">{row.description}</div><hr />
                    <div className="card-text mb-2">
                      {row.options.length === 0 ? <div className="alert alert-info small">Klik tombol tambah kandidat dibawah ini!</div> : (
                        <ul class="list-group list-group-flush">
                          {row.options.map((r, i) =>
                            <div>
                              <div className="modal fade" id={`putBallotOptionModal${r.id}`} tabIndex="-1" role="dialog" aria-labelledby="myLargeModalLabel" aria-hidden="true">
                                <div className="modal-dialog modal-lg" role="document">
                                  <div className="modal-content">
                                    <div className="modal-header">
                                      <h5 className="modal-title" id="exampleModalLabel">Edit Kandidat Pemilihan</h5>
                                      <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span aria-hidden="true">&times;</span>
                                      </button>
                                    </div>
                                    <div className="modal-body">
                                      <FormBallotOption unique_id={url.split('election/').pop().split('/')[0]} ballot_id={row.id} dataBallot={row} dataBallotOption={r} option_id={r.id} mode="edit" />
                                    </div>
                                  </div>
                                </div>
                              </div>
                              <li class="list-group-item d-flex">
                                <span className="mr-auto"><i className="fa fa-check-circle" />{' '}{r.name}</span>
                                <span>
                                  <div class="dropdown show">
                                    <button className="btn btn-sm order-1 order-lg-0 dropdown-toggle" id="dropdownBallotOption" data-toggle="dropdown"><i className="fa fa-bars"></i></button>
                                    <div class="dropdown-menu" aria-labelledby="dropdownBallotOption">
                                      <a class="dropdown-item" href="#" data-toggle="modal" data-target={`#putBallotOptionModal${r.id}`} onClick={e => setState({ ...state, idBallot: row.id, idBallotOption: r.id, dataBallot: row, dataBallotOption: r })}>Edit</a>
                                      <a class="dropdown-item" href="#" data-toggle="modal" data-target="#delBallotOptionModal" onClick={e => setState({ ...state, idBallot: row.id, idBallotOption: r.id })}>Hapus</a>
                                    </div>
                                  </div>
                                </span>
                              </li>
                            </div>
                          )}
                        </ul>
                      )}
                    </div>
                    <div className="card-text">
                      <button className="btn btn-primary btn-sm" data-toggle="modal" data-target={`#addBallotOptionModal${row.id}`} onClick={e => setState({ ...state, idBallot: row.id })}>Tambah Kandidat Pemilihan</button>
                      <div className="modal fade" id={`addBallotOptionModal${row.id}`} tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                        <div className="modal-dialog modal-lg" role="document">
                          <div className="modal-content">
                            <div className="modal-header">
                              <h5 className="modal-title" id="exampleModalLabel">Tambah Kandidat Pemilihan</h5>
                              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                              </button>
                            </div>
                            <div className="modal-body">
                              <FormBallotOption unique_id={url.split('election/').pop().split('/')[0]} ballot_id={row.id} mode="add" />
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        {/* {!props.listBallot&&<Loading/>} */}
      </div>
      <div className="modal fade" id="addBallotModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Tambah Jenis Pemilihan</h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <FormBallot unique_id={url.split('election/').pop().split('/')[0]} mode="add" />
            </div>
          </div>
        </div>
      </div>

      <div className="modal fade" id="delBallotModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-body">
              <div className="text-center">
                <div className="subtitle-text">Hapus Jenis Pemilihan</div>
                <div className="note-text">Apakah Anda ingin menghapus jenis pemilihan ini? </div>
                <div className="btn btn-group">
                  <div className="orange-btn" onClick={e => delBallot(url.split('election/').pop().split('/')[0], state.idBallot)}>Ya, Hapus</div>
                  <div className="light-btn" data-dismiss="modal" aria-label="Close">Batal</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="modal fade" id="delBallotOptionModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-body">
              <div className="text-center">
                <div className="subtitle-text">Hapus Kandidat Pemilihan</div>
                <div className="note-text">Apakah Anda ingin menghapus kandidat pemilihan ini? </div>
                <div className="btn btn-group">
                  <div className="orange-btn" onClick={e => delBallotOption(url.split('election/').pop().split('/')[0], state.idBallot, state.idBallotOption)}>Ya, Hapus</div>
                  <div className="light-btn" data-dismiss="modal" aria-label="Close">Batal</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {state.loading && <Loading />}
    </div>
  )
}

export default ListBallot