import React, { useEffect, useState } from 'react';
import store from 'store'
import { Base64 } from 'js-base64'
import * as Yup from 'yup'
import history from '../../../utils/history'
import { ShowNotify, notifyPosition, notifyType } from '../../../utils/notification'
import { REMEMBERME, EMAILUSER, PASSWORDUSER, TOKEN, REQFORM, MINFORM } from '../../../utils/constants'
import { Formik } from 'formik';
import moment from 'moment'
import { Link } from 'react-router-dom';
import DatePicker from 'react-datepicker'
import { ElectionAPI } from '../api/ElectionAPI';
import Loading from '../../../utils/Loading';
const {listTimezone, listTimezoneIndo} = require("../../../assets/mock/mock")
var momenttz = require('moment-timezone')

const today = new Date()
const tomorrow = new Date(today)
function FormElection({dataEdit,id, mode}) {
  console.log(dataEdit)
  const [state, setState] = useState({
    loading: false,
    isChecked: store.get(REMEMBERME) ? Base64.decode(store.get(REMEMBERME)) : false,
    start_date: new Date(),
    end_date: tomorrow.setDate(today.getDate()+1),
  })
  const ExampleCustomTimeInput = ({ date, value, onChange }) => (
    <input
      className={state.start_date || state.end_date ? "" : "d-none"}
      value={value}
      onChange={e => onChange(e.target.value)}
      style={{ border: "solid 1px pink" }}
    />
  );
  return (
    <div>
      <Formik
        initialValues={{
          title: dataEdit?dataEdit.title:'',
          start_date: dataEdit?new Date(dataEdit.start_date):new Date(),
          end_date: dataEdit?new Date(dataEdit.end_date):tomorrow.setDate(today.getDate()+1),
          time_zone: dataEdit?dataEdit.time_zone:''
        }}
        validationSchema={
          Yup.object({
            title: Yup.string().required(REQFORM),
            start_date: Yup.string().nullable().required(REQFORM),
            end_date: Yup.string().nullable().required(REQFORM),
            time_zone: Yup.string().required(REQFORM)
          })
        }
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(async () => {
            setState({ ...state, loading: true })
            const finalValues = {
              title: values.title,
              start_date: moment(values.start_date).format("DD-MM-YYYY HH:mm"),
              end_date: moment(values.end_date).format("DD-MM-YYYY HH:mm"),
              time_zone: values.time_zone,
            }
            const response = mode==="add"?await ElectionAPI.createElection(finalValues):mode==="edit"&&await ElectionAPI.patchElection(finalValues,id)
            if (response) {
              if (response.status === 500) {
                ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
              } else if (response.status === 404) {
                ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
              } else {
                ShowNotify(`Succesfully ${mode==="add"?"create":mode==="edit"&&"edit"} election!`, notifyPosition.topCenter, notifyType.success, () => {
                  history.push("/")
                  window.location.reload()
                })
              }
            }
            setState({ ...state, loading: false })
            setSubmitting(false);
          }, 400);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue
          /* and other goodies */
        }) => (
            <form onSubmit={handleSubmit}>
              <div className="form-group pl-4 pr-4">
                <div>Title</div>
                <input
                  className="form-control form-control-lg"
                  placeholder='Judul pemilihan anda'
                  type="text"
                  name="title"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.title}
                />
                <div className="err-form small">{errors.title && touched.title && errors.title}</div>
              </div>
              <div className="form-row pl-4 pr-4">
                <div className="form-group col-sm-12 col-md-6">
                  <div>Start Date</div>
                  <DatePicker
                    className="form-control form-control-lg"
                    selected={values.start_date?values.start_date:state.start_date}
                    dateFormat="dd-MM-yyyy, HH:mm"
                    onChange={date => {
                      setState({ ...state, start_date: date });
                      setFieldValue("start_date", date)
                    }}
                    isClearable
                    showTimeInput
                    customTimeInput={<ExampleCustomTimeInput />}
                    placeholderText="Pilih tanggal awal"
                  />
                  <div className="err-form">{errors.birth_date && touched.birth_date && errors.birth_date}</div>
                </div>
                <div className="form-group col-sm-12 col-md-6">
                  <div>End Date</div>
                  <DatePicker
                    className="form-control form-control-lg"
                    selected={values.end_date?values.end_date:state.end_date}
                    dateFormat="dd-MM-yyyy, HH:mm"
                    onChange={date => {
                      setState({ ...state, end_date: date });
                      setFieldValue("end_date", date)
                    }}
                    isClearable
                    showTimeInput
                    customTimeInput={<ExampleCustomTimeInput />}
                    placeholderText="Pilih tanggal akhir"
                  />
                  <div className="err-form">{errors.birth_date && touched.birth_date && errors.birth_date}</div>
                </div>
              </div>
              <div className="form-group pl-4 pr-4">
                <div>Timezone</div>
                <select
                  className="custom-select custom-select-lg"
                  defaultValue={values.time_zone!==""?values.time_zone:""}
                  onChange={e => setFieldValue("time_zone", e.target.value)}
                >
                  <option value="">Pilih zona waktu</option>
                  {listTimezoneIndo.data.sort((a,b) => (a.zoneName > b.zoneName) ? 1 : ((b.zoneName > a.zoneName) ? -1 : 0)).map((row,index)=>(
                  <option key={index} value={row.zoneName}>{row.zoneName} (GMT {momenttz(state.start_date).tz(row.zoneName).format('Z')}){" ("}
                  {momenttz(state.start_date).tz(row.zoneName).format('Z')==="+07:00"?"WIB":momenttz(state.start_date).tz(row.zoneName).format('Z')==="+08:00"?"WITA":momenttz(state.start_date).tz(row.zoneName).format('Z')==="+09:00"&&"WIT"}{") "}</option>
                  ))}
                  {listTimezone.data.sort((a,b) => (a.zoneName > b.zoneName) ? 1 : ((b.zoneName > a.zoneName) ? -1 : 0)).map((row,index)=>(
                  <option key={index} value={row.zoneName}>{row.zoneName} (GMT {momenttz(state.start_date).tz(row.zoneName).format('Z')})</option>
                  ))}
                </select>
                <div className="err-form">{errors.time_zone && touched.time_zone && errors.time_zone}</div>
              </div>
              <div className="form-group text-center">
                <button className="btn orange-btn m-2" type="submit" disabled={isSubmitting}>
                  Submit
                </button>
              </div>
              <div className="text-center small">
                Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved
              </div>
            </form>
          )}
      </Formik>
      {state.loading&&<Loading/>}
    </div>
  )
};

export default FormElection;