import React, { useEffect, useState } from 'react'
import axios from 'axios'
import moment from 'moment'
import NavbarElection from '../../components/NavbarElection'
import SidebarElection from '../../components/SidebarElection'
import { Link, Route, Switch, useRouteMatch } from 'react-router-dom'
import Ballot from '../../Ballot/Ballot'
import Voter from '../../Voter/Voter'
import { VoterAPI } from '../../Voter/api/VoterAPI'
import { BallotAPI } from '../../Ballot/api/BallotAPI'
import { ElectionAPI } from '../api/ElectionAPI'
import Loading from '../../../utils/Loading'
import {getDateTimeArrayIndo} from '../../../utils/index'
import { ShowNotify } from '../../../utils/notification'

function ContentElection() {
  const [state, setState] = useState({
    loading: false,
    data: [],
    detail: null
  })
  let { path, url } = useRouteMatch()
  function copyText() {
    var copyText = document.getElementById("inputElectionURL");
    copyText.select();
    copyText.setSelectionRange(0, 99999)
    document.execCommand("copy");
    ShowNotify("Berhasil copy link url pemilihan!")
  }
  const getData = async () => {
    setState({ ...state, loading: true })
    const r = await ElectionAPI.getElectionSummary(url.split("election/")[1])
    const rDetail = await ElectionAPI.getElectionBy(url.split("election/")[1])
    setState({
      ...state,
      data: r.data,
      detail: rDetail.data
    })
  }
  useEffect(() => {
    getData()
  }, [])

  if (!state.detail) {
    return (
      <div id="layoutSidenav_content">
        <Loading />
      </div>
    )
  } else {
    return (
      <Switch>
        <Route exact path={path}>
          <div id="layoutSidenav_content">
            <main>
              <div className="container-fluid">
                <h1 className="mt-4">{state.detail.title}</h1>
                <ol className="breadcrumb mb-4">
                  <li className="breadcrumb-item active">Dashboard</li>
                </ol>
                <div className="row">
                  <div className="col-xl-4 col-md-4">
                    <div className="card bg-primary text-white mb-4">
                      <div className="card-body">Pemilih</div>
                      <div className="card-body title-text text-white d-flex"><span className="mr-auto">{state.data.voters}</span><i className="fa fa-users" /></div>
                      <div className="card-footer d-flex align-items-center justify-content-between">
                        <Link className="small text-white stretched-link" to={`${url}/voter`}>View Details</Link>
                        <div className="small text-white"><i className="fa fa-angle-right"></i></div>
                      </div>
                    </div>
                  </div>
                  <div className="col-xl-4 col-md-4">
                    <div className="card bg-secondary text-white mb-4">
                      <div className="card-body">Jenis Pemilihan</div>
                      <div className="card-body title-text text-white d-flex"><span className="mr-auto">{state.data.ballots}</span><i className="fa fa-question-circle" /></div>
                      <div className="card-footer d-flex align-items-center justify-content-between">
                        <Link className="small text-white stretched-link" to={`${url}/ballot`}>View Details</Link>
                        <div className="small text-white"><i className="fa fa-angle-right"></i></div>
                      </div>
                    </div>
                  </div>
                  <div className="col-xl-4 col-md-4">
                    <div className="card bg-success text-white mb-4">
                      <div className="card-body">Kandidat Pemilihan</div>
                      <div className="card-body title-text text-white d-flex"><span className="mr-auto">{state.data.ballot_options}</span><i className="fa fa-cog" /></div>
                      <div className="card-footer d-flex align-items-center justify-content-between">
                        <Link className="small text-white stretched-link" to={`${url}/ballot`}>View Details</Link>
                        <div className="small text-white"><i className="fa fa-angle-right"></i></div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-xl-6 col-md-6">
                    <div className="card mb-4">
                      <div className="card-body">
                        <p className="card-title subtext-text"><i className="fa fa-calendar" />{' '}Mulai Tanggal</p><hr />
                        <p className="card-text">{getDateTimeArrayIndo(state.detail.start_date)}</p>
                      </div>
                    </div>
                  </div>
                  <div className="col-xl-6 col-md-6">
                    <div className="card mb-4">
                      <div className="card-body">
                        <p className="card-title subtext-text"><i className="fa fa-calendar" />{' '}Berakhir Tanggal</p><hr />
                        <p className="card-text">{getDateTimeArrayIndo(state.detail.end_date)}</p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-xl-12 col-md-12">
                    <div className="card mb-4">
                      <div className="card-body">
                        <p className="card-title subtext-text"><i className="fa fa-globe" />{' '}Link URL Pemilihan</p><hr />
                        <div className="input-group">
                          <div className="custom-file">
                            <input type="text" className="form-control" id="inputElectionURL" defaultValue={`http://localhost:3000/election-vote/${state.detail.unique_id}`} readOnly />
                          </div>
                          <div className="input-group-append">
                            <button className="btn btn-outline-secondary" type="button" onClick={e => copyText()}>Copy</button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </div>
            </main>
            <footer className="py-4 bg-light mt-auto">
              <div className="container-fluid">
                <div className="d-flex align-items-center justify-content-between small">
                  <div className="text-muted">Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved</div>
                  <div>
                    <a href="#">Privacy Policy</a>
                                &middot;
                                <a href="#">Terms &amp; Conditions</a>
                  </div>
                </div>
              </div>
            </footer>
          </div>
        </Route>
        <Route path={`${url}/ballot/`}>
          <Ballot />
        </Route>
        <Route path={`${url}/voter/`}>
          <Voter />
        </Route>
      </Switch>

    )
  }
}

export default ContentElection