import axios from 'axios'
import { getWithAuth, postWithAuth, deleteWithAuth,putWithAuth } from '../../../utils/requestHandler'

export const BallotAPI = {
  async createBallot(unique_id, data) {
    const response = await postWithAuth(`/api/elections/${unique_id}/ballots`, data)
    return response
  },

  async getBallot(unique_id) {
    const response = await getWithAuth(`/api/elections/${unique_id}/ballots`)
    return response
  },
  
  async putBallot(unique_id, data, ballot_id) {
    const response = await putWithAuth(`/api/elections/${unique_id}/ballots/${ballot_id}`, data)
    return response
  },
  
  async delBallot(unique_id, ballot_id) {
    const response = await deleteWithAuth(`/api/elections/${unique_id}/ballots/${ballot_id}`)
    return response
  },

  async createBallotOption(unique_id, ballot_id, data) {
    const response = await postWithAuth(`/api/elections/${unique_id}/ballots/${ballot_id}/options`, data)
    return response
  },

  async getBallotOption(unique_id, ballot_id) {
    const response = await getWithAuth(`/api/elections/${unique_id}/ballots/${ballot_id}/options`)
    return response
  },

  async putBallotOption(unique_id, ballot_id, option_id, data) {
    const response = await putWithAuth(`/api/elections/${unique_id}/ballots/${ballot_id}/options/${option_id}`,data)
    return response
  },

  async delBallotOption(unique_id, ballot_id, option_id) {
    const response = await deleteWithAuth(`/api/elections/${unique_id}/ballots/${ballot_id}/options/${option_id}`)
    return response
  }
}