import React, { useState } from 'react';
import store from 'store'
import { Base64 } from 'js-base64'
import Cookies from 'js-cookie'
import * as Yup from 'yup'
import history from '../../../../utils/history'
import { ShowNotify, notifyPosition, notifyType } from '../../../../utils/notification'
import { REMEMBERME, EMAILUSER, PASSWORDUSER, TOKEN, REQFORM, MINFORM, AUTH, USER } from '../../../../utils/constants'
import { Formik } from 'formik';
import { LoginAPI } from '../api/LoginAPI';
import { Link } from 'react-router-dom';
import Loading from '../../../../utils/Loading';

function FormLogin() {
  const [state, setState] = useState({
    loading: false,
    isChecked: store.get(REMEMBERME) ? Base64.decode(store.get(REMEMBERME)) : false,
    togglePass: false
  })
  return (
    <div>
      <Formik
        initialValues={{
          email: store.get(EMAILUSER) ? Base64.decode(store.get(EMAILUSER)) : '',
          password: store.get(PASSWORDUSER) ? Base64.decode(store.get(PASSWORDUSER)) : ''
        }}
        validationSchema={
          Yup.object({
            email: Yup.string().email().required(REQFORM),
            password: Yup.string().min(8, MINFORM).required(REQFORM)
          })
        }
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(async () => {
            if (state.isChecked) {
              store.set(EMAILUSER, Base64.encode(values.email))
              store.set(PASSWORDUSER, Base64.encode(values.password))
              store.set(REMEMBERME, Base64.encode(state.isChecked))
            }
            setState({ ...state, loading: true })
            const response = await LoginAPI.loginAccount(values)
            if (response) {
              if (response.status === 500) {
                ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
              } else if (response.status === 404) {
                ShowNotify("Email or password invalid.", notifyPosition.topCenter, notifyType.error)
              } else {
                ShowNotify("Anda telah berhasil login", notifyPosition.topCenter, notifyType.success, () => {
                  store.set(USER, response.data.user)
                  store.set(AUTH, Base64.encode(response.data.auth_token))
                  Cookies.set(TOKEN, Base64.encode(response.data.api_token))
                  history.push("/")
                  window.location.reload()
                })
              }
            }
            setState({ ...state, loading: false })
            setSubmitting(false);
          }, 400);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          /* and other goodies */
        }) => (
            <form onSubmit={handleSubmit}>
              <div className="form-group pl-4 pr-4">
                <input
                  className="form-control form-control-lg"
                  placeholder='&#xF0E0; Alamat email anda'
                  type="email"
                  name="email"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.email}
                />
                <div className="err-form small">{errors.email && touched.email && errors.email}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <div className="input-group">
                  <input
                    className="form-control form-control-lg"
                    placeholder='&#xF084; Kata sandi anda'
                    type={state.togglePass ? "text" : "password"}
                    name="password"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.password}
                  />
                  <div className="input-group-prepend" id="hider" onClick={e => setState({ ...state, togglePass: !state.togglePass })}>
                    <span className="input-group-text" id="validationTooltipUsernamePrepend"><i class={state.togglePass ? "fa fa-eye" : "fa fa-eye-slash"} aria-hidden="true"></i></span>
                  </div>
                </div>
                <div className="err-form small">{errors.password && touched.password && errors.password}</div>
              </div>
              <div className="form-group checking ml-4 pl-4 pr-4">
                <input type="checkbox" className="form-check-input" id="exampleCheck1" checked={state.isChecked} onChange={e => setState({ ...state, isChecked: !state.isChecked })} />
                <label className="form-check-label" for="exampleCheck1">Ingatkan saya</label>
                <div>Lupa Kata Sandi?</div>
              </div>
              <div className="form-group">
                <button className="btn orange-btn m-2" type="submit" disabled={isSubmitting}>
                  Masuk
                </button>
                <Link to="/register">
                  <button className="btn light-btn m-2" disabled={isSubmitting}>
                    Daftar
                </button>
                </Link>
              </div>
              <div className="small">
                Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved
              </div>
            </form>
          )}
      </Formik>
      {state.loading && <Loading />}
    </div>
  )
};

export default FormLogin;