import React, { useState } from 'react';
import store from 'store'
import { Base64 } from 'js-base64'
import Cookies from 'js-cookie'
import * as Yup from 'yup'
import history from '../../../../utils/history'
import { ShowNotify, notifyPosition, notifyType } from '../../../../utils/notification'
import { REMEMBERME, EMAILUSER, PASSWORDUSER, TOKEN, REQFORM, MINFORM, AUTH, USER, KEY,VOTER } from '../../../../utils/constants'
import { Formik } from 'formik';
import { LoginVotersAPI } from '../api/LoginVotersAPI';
import { Link } from 'react-router-dom';
import Loading from '../../../../utils/Loading';

function FormLogin() {
  const [state, setState] = useState({
    loading: false,
    isChecked: store.get(REMEMBERME) ? Base64.decode(store.get(REMEMBERME)) : false,
    togglePass: false
  })
  return (
    <div>
      <Formik
        initialValues={{
          election_unique_id: '',
          secret_key: ''
        }}
        validationSchema={
          Yup.object({
            election_unique_id: Yup.string().required(REQFORM),
            secret_key: Yup.string().required(REQFORM)
          })
        }
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(async () => {
            setState({ ...state, loading: true })
            const response = await LoginVotersAPI.loginAccount(values)
            const data = response.data

            if (response) {
              if (response.status === 500) {
                ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
              } else if (response.status === 404) {
                ShowNotify("Email or password invalid.", notifyPosition.topCenter, notifyType.error)
              } else {
                ShowNotify("Anda telah berhasil login", notifyPosition.topCenter, notifyType.success, () => {
                  Cookies.set(KEY, Base64.encode(data.voter.secret_key))
                  store.set(VOTER, data.voter)
                  history.push(`/election-vote/${data.election.unique_id}`)
                  window.location.reload()
                })
              }
            }
            setState({ ...state, loading: false })
            setSubmitting(false);
          }, 400);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          /* and other goodies */
        }) => (
            <form onSubmit={handleSubmit}>
              <div className="form-group pl-4 pr-4">
                <div className="input-group">
                  <input
                    className="form-control form-control-lg"
                    placeholder='&#xF187; ID Pemilihan'
                    type={state.togglePass ? "text" : "password"}
                    name="election_unique_id"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.election_unique_id}
                  />
                  <div className="input-group-prepend" id="hider" onClick={e => setState({ ...state, togglePass: !state.togglePass })}>
                    <span className="input-group-text" id="validationTooltipUsernamePrepend"><i class={state.togglePass ? "fa fa-eye" : "fa fa-eye-slash"} aria-hidden="true"></i></span>
                  </div>
                </div>
                <div className="err-form small">{errors.election_unique_id && touched.election_unique_id && errors.election_unique_id}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <div className="input-group">
                  <input
                    className="form-control form-control-lg"
                    placeholder='&#xF084; Kunci Rahasia'
                    type={state.togglePass ? "text" : "password"}
                    name="secret_key"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.secret_key}
                  />
                  <div className="input-group-prepend" id="hider" onClick={e => setState({ ...state, togglePass: !state.togglePass })}>
                    <span className="input-group-text" id="validationTooltipUsernamePrepend"><i class={state.togglePass ? "fa fa-eye" : "fa fa-eye-slash"} aria-hidden="true"></i></span>
                  </div>
                </div>
                <div className="err-form small">{errors.secret_key && touched.secret_key && errors.secret_key}</div>
              </div>
              <div className="form-group">
                <button className="btn orange-btn m-2" type="submit" disabled={isSubmitting}>
                  Masuk
                </button>
              </div>
              <div className="small">
                Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved
              </div>
            </form>
          )}
      </Formik>
      {state.loading && <Loading />}
    </div>
  )
};

export default FormLogin;