import React from 'react'
import { Route, Redirect } from 'react-router-dom';
import store from 'store'
import { AuthAPI } from '../views/Auth/api/AuthAPI';
import { AUTH, TOKEN } from './constants';

const PrivateRouteVote = ({ children, ...rest }) => (
  <Route {...rest} render={({ location }) => (
    AuthAPI.isAuthenticatedVoter() ?
    // store.get(TOKEN)?
      children : <Redirect to={{ pathname: '/login-voters', state: { from: location } }} />
  )} />
)
export default PrivateRouteVote