import axios from 'axios'
import store from 'store'
import Cookies from 'js-cookie'
import { Base64 } from 'js-base64'
import { AUTH, TOKEN, KEY } from '../../../utils/constants';
// import { PostWithoutAuth } from '../../../../utils/requestHandler'

export const AuthAPI = {
  getAuth() {
    return store.get(AUTH, null);
  },

  getToken() {
    return Cookies.get(TOKEN);
  },

  getSecretKey() {
    return Cookies.get(KEY);
  },

  isAuthenticated() {
    return !!this.getAuth() && !!this.getToken();
  },

  isAuthenticatedVoter() {
    return !!this.getSecretKey();
  },

  getHTTPHeader() {
    return {
      "x-auth-token": Base64.decode(this.getAuth()),
      "x-api-token": Base64.decode(this.getToken())
    };
  },

  // async fetchUser() {
  //   const response = await getData("/api/users/me/");
  //   return response;
  // }
}