import React, { useState } from 'react';
import store from 'store'
import { Base64 } from 'js-base64'
import * as Yup from 'yup'
import history from '../../../../utils/history'
import { ShowNotify, notifyPosition, notifyType } from '../../../../utils/notification'
import { REMEMBERME, EMAILUSER, PASSWORDUSER, TOKEN, REQFORM, MINFORM } from '../../../../utils/constants'
import { Formik } from 'formik';
import { RegistrationAPI } from '../api/RegisterAPI';
import { Link } from 'react-router-dom';
import Loading from '../../../../utils/Loading';

function FormRegister() {
  const [state, setState] = useState({
    loading: false,
    togglePass: false
  })
  return (
    <div>
      <Formik
        initialValues={{
          name: '',
          email: '',
          password: '',
          organization: ''
        }}
        validationSchema={
          Yup.object({
            name: Yup.string().required(REQFORM),
            email: Yup.string().email().required(REQFORM),
            password: Yup.string().min(8, MINFORM).required(REQFORM),
            organization: Yup.string().required(REQFORM)
          })
        }
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(async () => {
            setState({ ...state, loading: true })
            const response = await RegistrationAPI.registrationAccount(values)
            if (response) {
              if (response.status === 500) {
                ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
              } else if (response.status === 400) {
                ShowNotify("Email has been registered!.", notifyPosition.topCenter, notifyType.error)
              } else {
                ShowNotify("Anda telah berhasil registrasi", notifyPosition.topCenter, notifyType.success, () => {
                  history.push("/")
                  window.location.reload()
                })
              }
            }
            setState({ ...state, loading: false })
            setSubmitting(false);
          }, 400);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          /* and other goodies */
        }) => (
            <form onSubmit={handleSubmit}>
              <div className="form-group pl-4 pr-4">
                <input
                  className="form-control form-control-lg"
                  placeholder='&#xF007; Nama lengkap anda'
                  type="text"
                  name="name"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.name}
                />
                <div className="err-form small">{errors.name && touched.name && errors.name}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <input
                  className="form-control form-control-lg"
                  placeholder='&#xF0E0; Alamat email anda'
                  type="email"
                  name="email"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.email}
                />
                <div className="err-form small">{errors.email && touched.email && errors.email}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <div className="input-group" id="show_hide_password">
                  <input
                    className="form-control form-control-lg"
                    placeholder='&#xF084; Kata sandi anda'
                    type={state.togglePass?"text":"password"}
                    name="password"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.password}
                  />
                  <div className="input-group-prepend" id="hider" onClick={e=>setState({...state,togglePass:!state.togglePass})}>
                    <span className="input-group-text" id="validationTooltipUsernamePrepend"><i class={state.togglePass?"fa fa-eye":"fa fa-eye-slash"} aria-hidden="true"></i></span>
                  </div>
                </div>
                <div className="err-form small">{errors.password && touched.password && errors.password}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <input
                  className="form-control form-control-lg"
                  placeholder='&#xF1AD; Organisasi anda'
                  type="text"
                  name="organization"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.organization}
                />
                <div className="err-form small">{errors.organization && touched.organization && errors.organization}</div>
              </div>
              <div className="form-group">
                <button className="btn orange-btn m-2" type="submit" disabled={isSubmitting}>
                  Daftar
                </button>
                <div className="ml-2 mr-2" disabled={isSubmitting}>
                  <Link to="/login" className="primary-color" style={{textDecoration:"underline"}}>I have account!</Link>
                </div>
              </div>
              <div className="small">
                Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved
              </div>
            </form>
          )}
      </Formik>
      {state.loading&&<Loading/>}
    </div>
  )
};

export default FormRegister;