import React from 'react'
import { useRouteMatch } from 'react-router-dom'
import useSWR from 'swr'
import { VoterAPI } from '../api/VoterAPI'
import Loading from '../../../utils/Loading'
import ListVoter from './ListVoter'

// const { listBallot } = require("../../../assets/mock/mock")
function ContentVoter() {
  let {path,url} = useRouteMatch()
  const { data, error } = useSWR(`/api/elections${url.split('election/').pop().split('/')[0]}/voters`, ()=>VoterAPI.getVoter(url.split('election/').pop().split('/')[0]))

  if (error) return <div id="layoutSidenav_content" className="container-fluid">Failed to load<br/>Please check your connection!</div>
  if (!data) return <div id="layoutSidenav_content"><Loading/></div>
  console.log(data)
  return (
    <div id="layoutSidenav_content">
      <main>
        <div className="container-fluid">
          <h1 className="mt-4">Pemilih</h1>
          <ol className="breadcrumb mb-4">
            <li className="breadcrumb-item active">Pemilih</li>
          </ol>
          <div>
            <ListVoter listVoter={data.data} />
          </div>
        </div>
      </main>
      <footer className="py-4 bg-light mt-auto">
        <div className="container-fluid">
          <div className="d-flex align-items-center justify-content-between small">
            <div className="text-muted">Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved</div>
            <div>
              <a href="#">Privacy Policy</a>
               &middot;
              <a href="#">Terms &amp; Conditions</a>
            </div>
          </div>
        </div>
      </footer>
    </div>
  )
}

export default ContentVoter