import React, { useState } from 'react';
import store from 'store'
import { Base64 } from 'js-base64'
import * as Yup from 'yup'
import { ShowNotify, notifyPosition, notifyType } from '../../../utils/notification'
import { REMEMBERME, REQFORM } from '../../../utils/constants'
import { Formik } from 'formik';
import Loading from '../../../utils/Loading';
import { VoterAPI } from '../api/VoterAPI';

function FormVoter({unique_id,voter_id,dataVoter,mode}) {
  const [state, setState] = useState({
    loading: false,
    isChecked: store.get(REMEMBERME) ? Base64.decode(store.get(REMEMBERME)) : false,
  })
  return (
    <div>
      <Formik
        initialValues={{
          name: dataVoter?dataVoter.name:'',
          email: dataVoter?dataVoter.email:'',
        }}
        validationSchema={
          Yup.object({
            name: Yup.string().required(REQFORM),
            email: Yup.string().required(REQFORM),
          })
        }
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(async () => {
            setState({ ...state, loading: true })
            const response = mode==="add"?await VoterAPI.createVoter(unique_id,values):mode==="edit"&&await VoterAPI.patchVoter(unique_id,values,voter_id)
            if (response) {
              if (response.status === 500) {
                ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
              } else if (response.status_code === 401) {
                ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
              } else{
                ShowNotify(`Succesfully ${mode==="add"?"create":mode==="edit"&&"edit"} ballot!`, notifyPosition.topCenter, notifyType.success, () => {
                  window.location.reload()
                })
              }
              // console.log(response)
            }
            setState({ ...state, loading: false })
            setSubmitting(false);
          }, 400);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue
          /* and other goodies */
        }) => (
            <form onSubmit={handleSubmit}>
              <div className="form-group pl-4 pr-4">
                <div>Nama Lengkap</div>
                <input
                  className="form-control form-control-lg"
                  placeholder='Masukkan nama lengkap anda'
                  type="text"
                  name="name"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.name}
                />
                <div className="err-form small">{errors.name && touched.name && errors.name}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <div>Alamat Email</div>
                <input
                  className="form-control form-control-lg"
                  placeholder='Deskripsi anda'
                  type="text"
                  name="email"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.email}
                />
                <div className="err-form small">{errors.email && touched.email && errors.email}</div>
              </div>
              <div className="form-group text-center">
                <button className="btn orange-btn m-2" type="submit" disabled={isSubmitting}>
                  Submit
                </button>
              </div>
              <div className="text-center small">
                Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved
              </div>
            </form>
          )}
      </Formik>
      {state.loading&&<Loading/>}
    </div>
  )
};

export default FormVoter;