import React, { useState } from 'react'
import store from 'store'
import Cookies from 'js-cookie'
import history from '../../../utils/history'
import { AUTH, KEY, TOKEN, USER, VOTER } from '../../../utils/constants'
import Loading from '../../../utils/Loading'
import { notifyPosition, notifyType, ShowNotify } from '../../../utils/notification'
import { LogoutAPI } from './api/LogoutAPI'

function LogoutVoter({ handleCloseLogout }) {
  const [state, setState] = useState({
    loading: false
  })

  async function logout() {
    setState({ ...state, loading: true })
    store.remove(VOTER);
    Cookies.remove(KEY)
    setState({ ...state, loading: false })
    window.location.reload()
  }
  return (
    <div className="text-center">
      <div className="title-text">Keluar Akun</div>
      <div className="note-text">Apakah Anda ingin keluar akun dari situs ini? </div>
      <div className="btn btn-group">
        <div className="orange-btn" onClick={logout}>Ya, Keluar</div>
        <div className="light-btn" data-dismiss="modal" aria-label="Close">Batal</div>
      </div>
      {state.loading&&<Loading/>}
    </div>
  )
}

export default LogoutVoter