import React, { useState } from 'react';
import store from 'store'
import { Base64 } from 'js-base64'
import * as Yup from 'yup'
import { ShowNotify, notifyPosition, notifyType } from '../../../utils/notification'
import { REMEMBERME, REQFORM } from '../../../utils/constants'
import { Formik } from 'formik';
import Loading from '../../../utils/Loading';
import { BallotAPI } from '../api/BallotAPI';
import { UploadImageAPI } from '../../api/UploadImageAPI';
import { Editor } from '@tinymce/tinymce-react';

const fotodefault = require("../../../assets/img/icons/user.png")

function FormBallotOption({ unique_id, ballot_id, option_id, dataBallot, dataBallotOption, mode }) {
  const [state, setState] = useState({
    loading: false,
    isChecked: store.get(REMEMBERME) ? Base64.decode(store.get(REMEMBERME)) : false,
    profilePicture: "",
    link: ""
  })
  const uploadedImageProfile = React.useRef(null);
  const changeImg = e => {
    const [file] = e.target.files;
    if (file) {
      const reader = new FileReader();
      const { current } = uploadedImageProfile;
      current.file = file;
      reader.onload = (e) => {
        current.src = e.target.result;
      }
      reader.readAsDataURL(file);
      setState({ ...state, profilePicture: file.name })
      return file
    }
  }
  const handleImageProfilePicture = async (e) => {
    const [file] = e.target.files;
    if (file) {
      const reader = new FileReader();
      const { current } = uploadedImageProfile;
      current.file = file;
      reader.onload = (e) => {
        current.src = e.target.result;
      }
      reader.readAsDataURL(file);
      var files = new FormData()
      files.append("file", file)
      setState({ ...state, loading: true })
      const response = await UploadImageAPI.uploaded(files)
      if (response) {
        if (response.status === 500) {
          ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
          setState({ ...state, profilePicture: "" })
        } else if (response.status === 401) {
          ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
          setState({ ...state, profilePicture: "" })
        } else {
          ShowNotify("Anda telah berhasil upload file", notifyPosition.topCenter, notifyType.success, () => {
            setState({ ...state, profilePicture: file.name })
          })
        }
        setState({ ...state, loading: false })
        return response
      }
    }
  };

  const handleEditorChange = (content, editor) => {
    console.log('Content was updated:', content);
  }
  return (
    <div>
      <Formik
        initialValues={{
          name: dataBallotOption ? dataBallotOption.name : '',
          short_description: dataBallotOption ? dataBallotOption.short_description : '',
          description: dataBallotOption ? dataBallotOption.description : '',
          photo: dataBallotOption ? dataBallotOption.photo : state.link
        }}
        validationSchema={
          Yup.object({
            name: Yup.string().required(REQFORM),
            short_description: Yup.string().required(REQFORM),
            description: Yup.string().required(REQFORM),
            photo: Yup.string().required(REQFORM),
          })
        }
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(async () => {
            console.log(values)
            setState({ ...state, loading: true })
            if(mode === "add"){
              const response = await BallotAPI.createBallotOption(unique_id, ballot_id, values)
              if (response) {
                if (response.status === 500) {
                  ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
                } else if (response.status_code === 401) {
                  ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
                } else {
                  ShowNotify(`Succesfully create ballot option!`, notifyPosition.topCenter, notifyType.success, () => {
                    window.location.reload()
                  })
                }
              }
            }else if(mode ==="edit"){
              const response = await BallotAPI.putBallotOption(unique_id, ballot_id, option_id, values)
              if (response) {
                if (response.status === 500) {
                  ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
                } else if (response.status_code === 401) {
                  ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
                } else {
                  ShowNotify(`Succesfully edit ballot option!`, notifyPosition.topCenter, notifyType.success, () => {
                    window.location.reload()
                  })
                }
              }
            }
            setState({ ...state, loading: false })
            setSubmitting(false);
          }, 400);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue
          /* and other goodies */
        }) => (
            <form onSubmit={handleSubmit}>{console.log(values)}
              <div className="form-group pl-4 pr-4">
                <div>Nama Lengkap</div>
                <input
                  className="form-control form-control-lg"
                  placeholder='Masukkan nama lengkap'
                  type="text"
                  name="name"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.name}
                />
                <div className="err-form small">{errors.name && touched.name && errors.name}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <div>Deskripsi Singkat</div>
                <input
                  className="form-control form-control-lg"
                  placeholder='Masukkan deskripsi singkat'
                  type="text"
                  name="short_description"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.short_description}
                />
                <div className="err-form small">{errors.short_description && touched.short_description && errors.short_description}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <div>Deskripsi</div>
                <Editor
                  apiKey='1dcuu6g777g6opk0ow39pang5rw919bxhclfjx69ag27e078'
                  initialValue="<p>This is the initial content of the editor</p>"
                  init={{
                    height: 200,
                    menubar: false,
                    plugins: [
                      'advlist autolink lists link image charmap print preview anchor',
                      'searchreplace visualblocks code fullscreen',
                      'insertdatetime media table paste code help wordcount'
                    ],
                    toolbar:
                      'undo redo | formatselect | bold italic backcolor | \
                      alignleft aligncenter alignright alignjustify | \
                      bullist numlist outdent indent | removeformat | help'
                  }}
                  onEditorChange={(content, editor) => setFieldValue("description", content)}
                  onChange={handleChange}
                  value={values.description}
                />
                <div className="err-form small">{errors.description && touched.description && errors.description}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <div className="avatar-form">
                  <div className="avatarnya">
                    <img ref={uploadedImageProfile} src={values.photo ? values.photo : fotodefault} style={{ border: "1px solid gray" }} />
                  </div>
                  <div className="label-avatar">
                    <label>Foto</label>{console.log("html", values)}
                    <span style={{ display: "flex", alignItems: "center" }}>
                      <label for={`upload-pp${mode}${ballot_id}${option_id}`} className="upload-photos">Choose File</label>
                      <span className="pl-3">{state.profilePicture}</span>
                    </span>
                    <input
                      id={`upload-pp${mode}${ballot_id}${option_id}`}
                      style={{ display: "none" }}
                      type="file"
                      accept="image/*"
                      onChange={async (e) => {
                        const response = await handleImageProfilePicture(e);
                        console.log(response)
                        setState({...state,link:response.url})
                        setFieldValue("photo", response.url)
                        // changeImg(e)
                        // setFieldValue("photo", e.target.files)
                      }} />
                    <span></span>
                  </div>
                </div>
                <div className="err-form">{errors.profile_picture && touched.profile_picture && errors.profile_picture}</div>
              </div>
              <div className="form-group text-center">
                <button className="btn orange-btn m-2" type="submit" disabled={isSubmitting}>
                  Submit
                </button>
              </div>
              <div className="text-center small">
                Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved
              </div>
            </form>
          )}
      </Formik>
      {state.loading && <Loading />}
    </div>
  )
};

export default FormBallotOption;