import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import Logout from '../Auth/Logout/Logout';

function NavbarElection() {
  const [state, setState] = useState({
    toggle: false,
    navitems: false
  })
  useEffect(() => {
    var toogle = document.getElementById("bodyTag");
    var navitems = document.getElementById("nav-items");
    var navitemsoption = document.getElementById("nav-items-option");
    if (state.toggle) {
      toogle.classList.add("sb-sidenav-toggled");
    } else {
      toogle.classList.remove("sb-sidenav-toggled");
    }
    if (state.navitems) {
      navitems.classList.add("show");
      navitemsoption.classList.add("show");
    } else {
      navitems.classList.remove("show");
      navitemsoption.classList.remove("show");
    }
  }, [state.toggle, state.navitems])
  return (
    <nav className="sb-topnav navbar navbar-expand navbar-dark bg-dark">
      <Link to="/" className="navbar-brand">Online Election</Link>
      <button className="btn btn-link btn-sm order-1 order-lg-0" id="sidebarToggle" href="#" onClick={e => setState({ ...state, toggle: !state.toggle })}><i className="fa fa-bars"></i></button>
      <form className="d-none d-md-inline-block form-inline ml-auto mr-0 mr-md-3 my-2 my-md-0">
        {/* <div className="input-group">
          <input className="form-control" type="text" placeholder="Search for..." aria-label="Search" aria-describedby="basic-addon2" />
          <div className="input-group-append">
            <button className="btn btn-primary" type="button"><i className="fa fa-search"></i></button>
          </div>
        </div> */}
      </form>
      <ul className="navbar-nav ml-auto ml-md-0">
        <li id="nav-items" className="nav-item dropdown" onClick={e => setState({ ...state, navitems: !state.navitems })}>
          <a className="nav-link dropdown-toggle" id="userDropdown" href="#" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"><i className="fa fa-user fa-fw"></i></a>
          <div id="nav-items-option" className="dropdown-menu dropdown-menu-right" aria-labelledby="userDropdown">
            <a className="dropdown-item" href="#">Pengaturan</a>
            <div className="dropdown-divider"></div>
            <a className="dropdown-item" href="#" data-toggle="modal" data-target="#logoutModal">Keluar</a>
          </div>
        </li>
      </ul>
      <div className="modal fade" id="logoutModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-body">
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
              <Logout />
            </div>
          </div>
        </div>
      </div>
    </nav>
  )
}

export default NavbarElection