import React, { useState } from 'react';
import store from 'store'
import { Base64 } from 'js-base64'
import * as Yup from 'yup'
import { ShowNotify, notifyPosition, notifyType } from '../../../utils/notification'
import { REMEMBERME, REQFORM } from '../../../utils/constants'
import { Formik } from 'formik';
import Loading from '../../../utils/Loading';
import { BallotAPI } from '../api/BallotAPI';

function FormBallot({unique_id,ballot_id,dataBallot,mode}) {
  console.log(dataBallot)
  const [state, setState] = useState({
    loading: false,
    isChecked: store.get(REMEMBERME) ? Base64.decode(store.get(REMEMBERME)) : false,
  })
  return (
    <div>
      <Formik
        initialValues={{
          title: dataBallot?dataBallot.title:'',
          description: dataBallot?dataBallot.description:''
        }}
        validationSchema={
          Yup.object({
            title: Yup.string().required(REQFORM),
            description: Yup.string().required(REQFORM),
          })
        }
        onSubmit={(values, { setSubmitting }) => {
          setTimeout(async () => {
            setState({ ...state, loading: true })
            const response = mode==="add"?await BallotAPI.createBallot(unique_id,values):mode==="edit"&&await BallotAPI.putBallot(unique_id,values,ballot_id)
            if (response) {
              if (response.status === 500) {
                ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
              } else if (response.status_code === 401) {
                ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
              } else {
                ShowNotify(`Succesfully ${mode==="add"?"create":mode==="edit"&&"edit"} ballot!`, notifyPosition.topCenter, notifyType.success, () => {
                  window.location.reload()
                })
              }
              // console.log(response)
            }
            setState({ ...state, loading: false })
            setSubmitting(false);
          }, 400);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue
          /* and other goodies */
        }) => (
            <form onSubmit={handleSubmit}>
              <div className="form-group pl-4 pr-4">
                <div>Judul</div>
                <input
                  className="form-control form-control-lg"
                  placeholder='Judul pemilihan anda'
                  type="text"
                  name="title"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.title}
                />
                <div className="err-form small">{errors.title && touched.title && errors.title}</div>
              </div>
              <div className="form-group pl-4 pr-4">
                <div>Deskripsi</div>
                <textarea
                  className="form-control form-control-lg"
                  placeholder='Deskripsi anda'
                  type="text"
                  name="description"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.description}
                />
                <div className="err-form small">{errors.description && touched.description && errors.description}</div>
              </div>
              <div className="form-group text-center">
                <button className="btn orange-btn m-2" type="submit" disabled={isSubmitting}>
                  Submit
                </button>
              </div>
              <div className="text-center small">
                Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved
              </div>
            </form>
          )}
      </Formik>
      {state.loading&&<Loading/>}
    </div>
  )
};

export default FormBallot;