import React, { useEffect, useState } from 'react'
import axios from 'axios'
import moment from 'moment'
import NavbarElection from '../../components/NavbarElection'
import SidebarElection from '../../components/SidebarElection'
import { Link, Route, Switch, useRouteMatch } from 'react-router-dom'
import Ballot from '../../Ballot/Ballot'
import Voter from '../../Voter/Voter'
import { VoterAPI } from '../../Voter/api/VoterAPI'
import { BallotAPI } from '../../Ballot/api/BallotAPI'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import Cookies from 'js-cookie'
import Loading from '../../../utils/Loading'
import { getDateTimeArrayIndo } from '../../../utils/index'
import { notifyPosition, notifyType, ShowNotify } from '../../../utils/notification'
import { ElectionVoteAPI } from '../api/ElectionVoteAPI'
import { KEY } from '../../../utils/constants'
import { Base64 } from 'js-base64'
import CandidateDetail from './CandidateDetail'

const report = require("../../../assets/img/icons/report.svg")

function ContentElectionVote() {
  const [state, setState] = useState({
    loading: false,
    data: null,
  })
  const [tabIndex, setTabIndex] = useState(0);
  let { path, url } = useRouteMatch()

  async function candidateVote(ballotid,optionid){
    setState({...state,loading:true})
    const data = {
      "ballot_id":ballotid,
      "option_id":optionid
    }
    const key = Base64.decode(Cookies.get(KEY))
    console.log(key)
    console.log(url.split("election-vote/")[1])
    const response = await ElectionVoteAPI.postElectionVote(url.split("election-vote/")[1], key,data)
      if (response.status === 500) {
        ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
      } else if (response.status === 400) {
        ShowNotify("Gagal, Anda telah memilih pada jenis pemilihan ini!", notifyPosition.topCenter, notifyType.error)
      } else {
        ShowNotify("Anda berhasil memilih kandidat pada jenis pemilihan ini", notifyPosition.topCenter, notifyType.success, () => {
          window.location.reload()
        })
      }
    setState({...state,loading:false})
    // window.location.reload()
  }

  const getData = async () => {
    setState({ ...state, loading: true })
    const r = await ElectionVoteAPI.getElectionVoter(url.split("election-vote/")[1], Base64.decode(Cookies.get(KEY)))
    setState({
      ...state,
      data: r.data,
    })
  }
  useEffect(() => {
    getData()
  }, [])

  if (!state.data) {
    return (
      <div id="layoutSidenav_content">
        <Loading />
      </div>
    )
  } else {
    return (
      <Switch>
        <Route exact path={path}>
          <div id="layoutSidenav_content">
            <main>
              <div className="container-fluid">
                <h1 className="mt-4">{state.data.title}</h1>
                <ol className="breadcrumb mb-4">
                  <li className="breadcrumb-item active">{state.data.title}</li>
                </ol>
                <div className="row">
                  <div className="col-xl-6 col-md-6">
                    <div className="card mb-4">
                      <div className="card-body">
                        <p className="card-title subtext-text"><i className="fa fa-calendar" />{' '}Mulai Tanggal</p><hr />
                        <p className="card-text">{getDateTimeArrayIndo(state.data.start_date)}</p>
                      </div>
                    </div>
                  </div>
                  <div className="col-xl-6 col-md-6">
                    <div className="card mb-4">
                      <div className="card-body">
                        <p className="card-title subtext-text"><i className="fa fa-calendar" />{' '}Berakhir Tanggal</p><hr />
                        <p className="card-text">{getDateTimeArrayIndo(state.data.end_date)}</p>
                      </div>
                    </div>
                  </div>
                </div>
                <div>{console.log(state.data)}
                  <Tabs selectedIndex={tabIndex} onSelect={index => setTabIndex(index)}>
                    <TabList>
                      {state.data.ballots && state.data.ballots.map((row) => (
                        <Tab>{row.title}</Tab>
                      ))}
                    </TabList>
                    {state.data.ballots && state.data.ballots.map((row) => (
                      <TabPanel>
                        <h2>{row.title}</h2>
                        <p className="note-text">{row.description}</p>
                        <div className="row" style={row.options.length === 0 ? { height: "50vh" } : {}}>
                          {row.options.length === 0 ?
                            <div className="d-flex align-items-center justify-content-center w-100 flex-column">
                              <div><img src={report} /></div>
                              <div>Maaf daftar kandidat pemilihan belum ada!</div>
                            </div>
                            : row.options.map((arr) => (
                              <div className="col-lg-4 col-md-6 col-xs-12 mb-3">
                                <div className="card card-body h-100">
                                  {row.vote_option === arr.id && <b style={{ position: "absolute", right: 10, top: 5 }}>Pilihan Anda <i class="fa fa-check-circle success-color"></i></b>}
                                  <div>
                                    <img src={arr.photo} height="300" className="w-100 mt-3" />
                                  </div>
                                  <div className="subtitle-text mt-4">{arr.name}</div>
                                  <p className="note-text">{arr.short_description}</p>
                                  <div dangerouslySetInnerHTML={{ __html: arr.description }} />
                                  <div>
                                    <button className="btn btn-primary" data-toggle="modal" data-target={`#vote${row.id}${arr.id}`}>Pilih</button>
                                    <div className="modal fade" id={`vote${row.id}${arr.id}`} tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                      <div className="modal-dialog" role="document">
                                        <div className="modal-content">
                                          <div className="modal-body text-center">
                                            <h5>Apakah anda yakin memilih kandidat ini?</h5>
                                            <div>
                                              <img src={arr.photo} height="300" className="w-100 mt-3" />
                                            </div>
                                            <div className="subtitle-text mt-4">{arr.name}</div>
                                            <p className="note-text">{arr.short_description}</p>
                                            <div className="dflex">
                                              <button className="btn btn-primary mr-2" onClick={async()=>await candidateVote(row.id,arr.id)}>Ya, Pilih</button>
                                              <button className="btn btn-danger ml-2" type="button"  data-dismiss="modal" aria-label="Close">Tidak</button>
                                            </div>
                                            {state.loading&&<Loading/>}
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                    {/* <Link to={`${url}/detail-kandidat`}>
                                    <button>Detail</button>
                                    </Link> */}
                                  </div>
                                </div>
                              </div>
                            ))
                          }
                        </div>
                      </TabPanel>
                    ))}
                  </Tabs>
                </div>
              </div>
            </main>
            <footer className="py-4 bg-light mt-auto">
              <div className="container-fluid">
                <div className="d-flex align-items-center justify-content-between small">
                  <div className="text-muted">Copyrights © {new Date().getFullYear()} PT. Dukung Calonmu Indonesia All Rights Reserved</div>
                  <div>
                    <a href="#">Privacy Policy</a>
                                &middot;
                                <a href="#">Terms &amp; Conditions</a>
                  </div>
                </div>
              </div>
            </footer>
            
          </div>
        </Route>
        <Route path={`${url}/detail-kandidat/`}>
          <CandidateDetail />
        </Route>
        {/* <Route path={`${url}/ballot/`}>
          <Ballot />
        </Route>
        <Route path={`${url}/voter/`}>
          <Voter />
        </Route> */}
      </Switch>

    )
  }
}

export default ContentElectionVote