import React from 'react'
import PrivateRoute from './utils/PrivateRoute'
import PrivateRouteVote from './utils/PrivateRouteVote'
import { Redirect, Route, Switch } from 'react-router-dom'
import Dashboard from './views/Dashboard/Dashboard'
import Register from './views/Auth/Register/Register'
import Login from './views/Auth/Login/Login'
import LoginVoters from './views/Auth/LoginVoters/LoginVoters'
import Election from './views/Election/Election'
import Error from './views/Error'
import { AuthAPI } from './views/Auth/api/AuthAPI'
import ElectionVote from './views/ElectionVote/ElectionVote'

function MainApp() {
  return (
    <Switch>
      {/* private route election */}
      <PrivateRoute exact path="/"><Redirect to="/dashboard" /></PrivateRoute>
      <PrivateRoute path="/dashboard"><Dashboard /></PrivateRoute>
      <PrivateRoute path="/election/:unique_id"><Election /></PrivateRoute>
      {/* -------------------------------------------------------------- */}
      {/* private route electionvote */}
      <PrivateRouteVote exact path="/election-vote/:unique_id"><ElectionVote /></PrivateRouteVote>
      {/* -------------------------------------------------------------- */}
      {/* public route */}
      <Route path="/register">{AuthAPI.isAuthenticated() ? <Redirect to="/" /> : <Register />}</Route>
      <Route path="/login">{AuthAPI.isAuthenticated() ? <Redirect to="/" /> : <Login />}</Route>
      <Route path="/login-voters">{AuthAPI.isAuthenticatedVoter() ? <Redirect to="/election-vote/:unique_id" /> : <LoginVoters />}</Route>
      {/* -------------------------------------------------------------- */}
      <Route><Error /></Route>
    </Switch>
  )
}

export default MainApp