import React, { useEffect, useState } from 'react'
import { Link, useParams, useRouteMatch } from 'react-router-dom'
import { useMediaQuery } from 'react-responsive'
import Loading from '../../../utils/Loading'
import moment from 'moment'
import { VoterAPI } from '../api/VoterAPI'
import { ShowNotify, notifyPosition, notifyType } from '../../../utils/notification'
import FormVoter from './FormVoter'


const noneListImg = require("../../../assets/img/icons/report.svg")

function ListVoter(props) {
  let { path, url } = useRouteMatch()
  const isTablet = useMediaQuery({ query: '(max-width: 725px)' })
  const [state, setState] = useState({
    loading: false,
    dataVoter: null,
    idVoter: '',
  })
  const delVoter = async (unique_id, voter_id) => {
    setState({ ...state, loading: true })
    const response = await VoterAPI.delVoter(unique_id, voter_id)
    if (response) {
      if (response.status === 500) {
        ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
      } else if (response.status_code === 401) {
        ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
      } else if (response.status === 200) {
        ShowNotify("Succesfully delete ballot", notifyPosition.topCenter, notifyType.success, () => {
          window.location.reload()
        })
      }
    }
    setState({ ...state, loading: false })
  }
  // const delBallotOption = async (unique_id, ballot_id, option_id) => {
  //   setState({ ...state, loading: true })
  //   const response = await BallotAPI.delBallotOption(unique_id, ballot_id, option_id)
  //   if (response) {
  //     if (response.status === 500) {
  //       ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
  //     } else if (response.status_code === 401) {
  //       ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
  //     } else {
  //       ShowNotify("Succesfully delete ballot option!", notifyPosition.topCenter, notifyType.success, () => {
  //         window.location.reload()
  //       })
  //     }
  //   }
  //   setState({ ...state, loading: false })
  // }
  useEffect(() => {
  }, [state.idVoter])
  console.log(state.idVoter)
  return (
    <div>
      <div className="list-election card card-body">
        <div className={props.listVoter && props.listVoter.length === 0 ? "d-none" : isTablet ? "d-block" : "d-flex"}>
          <div className="mr-auto">
            <button className="btn btn-success my-2 my-sm-0" data-toggle="modal" data-target="#addVoterModal">Tambah Pemilih</button>
          </div>
          <form className={isTablet ? "mt-2 form-inline" : "ml-2 form-inline"}>
            <input className={isTablet ? "form-control w-100 mb-1" : "form-control mr-sm-2"} type="search" placeholder="Cari..." aria-label="Search" />
            <button className="btn btn-outline-success my-2 my-sm-0" type="submit">Cari</button>
          </form>
        </div>
        {props.listVoter && props.listVoter.length === 0 ? (
          <div className="d-flex align-items-center justify-content-center" style={{ height: "80vh" }}>
            <div className="text-center">
              <img src={noneListImg} width="100" height="100" />
              <br /><br /><p className="note-text">Klik tombol tambah pemilih dibawah ini!</p>
              <button className="btn btn-success my-2 my-sm-0" data-toggle="modal" data-target="#addVoterModal">Tambah Pemilih</button>
            </div>
          </div>
        ) :
          (
            <div className="pt-3 table-hover table-responsive">
              <table className="table">
                <thead className="table-dark">
                  <tr>
                    <th scope="col">No</th>
                    <th scope="col">Nama</th>
                    <th scope="col">Alamat Email</th>
                    <th scope="col">Kunci Rahasia</th>
                    <th scope="col">Status</th>
                    <th className="text-center" scope="col">Aksi</th>
                  </tr>
                </thead>
                <tbody>
                  {props.listVoter && props.listVoter.map((row, index) => (
                    <tr key={index}>
                      <th scope="row">{index + 1}</th>
                      <td>{row.name}</td>
                      <td>{row.email}</td>
                      <td>{row.secret_key}</td>
                      <td>{row.was_voting === true ? "Sudah Vote" : "Belum Vote"}</td>
                      <td className="text-center">
                        {/* <Link to={`/election/${row.unique_id}`}><button className="btn btn-primary btn-sm m-1">Detail</button></Link> */}
                        <button className="btn btn-warning btn-sm m-1" data-toggle="modal" data-target={`#patchVoterModal${row.id}`} onClick={e => setState({ ...state, idVoter: row.id, dataVoter: row })}>Edit</button>
                        <button className="btn btn-danger btn-sm m-1" data-toggle="modal" data-target="#delVoterModal" onClick={e => setState({ ...state, idVoter: row.id })}>Hapus</button>

                        <div className="modal fade" id={`patchVoterModal${row.id}`} tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                          <div className="modal-dialog" role="document">
                            <div className="modal-content">
                              <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">Edit Pemilih</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                  <span aria-hidden="true">&times;</span>
                                </button>
                              </div>
                              <div className="modal-body">
                                {state.dataVoter &&
                                  <FormVoter unique_id={url.split('election/').pop().split('/')[0]} dataVoter={row} voter_id={row.id} mode="edit" />
                                }
                              </div>
                            </div>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
              <nav aria-label="...">
                <ul className="pagination">
                  <li className="page-item disabled">
                    <a className="page-link" href="#" tabIndex="-1">Previous</a>
                  </li>
                  <li className="page-item"><a className="page-link" href="#">1</a></li>
                  <li className="page-item active">
                    <a className="page-link" href="#">2 <span className="sr-only">(current)</span></a>
                  </li>
                  <li className="page-item"><a className="page-link" href="#">3</a></li>
                  <li className="page-item">
                    <a className="page-link" href="#">Next</a>
                  </li>
                </ul>
              </nav>
            </div>
          )}
        {/* {!props.listBallot&&<Loading/>} */}
      </div>
      <div className="modal fade" id="addVoterModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">Tambah Pemilih</h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <FormVoter unique_id={url.split('election/').pop().split('/')[0]} mode="add" />
            </div>
          </div>
        </div>
      </div>
      <div className="modal fade" id="delVoterModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-body">
              <div className="text-center">
                <div className="subtitle-text">Hapus Pemilih</div>
                <div className="note-text">Apakah Anda ingin menghapus pemilih ini? </div>
                <div className="btn btn-group">
                  <div className="orange-btn" onClick={e => delVoter(url.split('election/').pop().split('/')[0], state.idVoter)}>Ya, Hapus</div>
                  <div className="light-btn" data-dismiss="modal" aria-label="Close">Batal</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {state.loading && <Loading />}
    </div>
  )
}

export default ListVoter