import { getWithAuthParams, postWithAuthParams } from '../../../utils/requestHandler'

export const ElectionVoteAPI = {
  async postElectionVote(electionid,secretkey,data) {
    const response = await postWithAuthParams("/api/elections/voters/voting",{
      "election-unique-id":electionid,
      "voter-secret-key":secretkey
    }, data)
    return response
  },

  async getElectionVoter(electionid,secretkey) {
    const response = await getWithAuthParams(`/api/elections/voters/voting`,{
      "election-unique-id":electionid,
      "voter-secret-key":secretkey
    })
    return response
  },
}