import React, { useEffect, useState } from 'react'
import ListElection from './ListElection'
import { ElectionAPI } from '../../Election/api/ElectionAPI'
import useSWR from 'swr'
import Loading from '../../../utils/Loading'

function ContentDashboard(){
  const [state,setState]=useState({
    loading:false,
    listElection:null
  })
  async function getListElection(){
    setState({...state,loading:true})
    const r = await ElectionAPI.getElection()
    setState({...state,loading:false, listElection:r})
  }
  useEffect(()=>{
    getListElection()
  },[])
  return(
    <div>
      <ListElection listElection={state.listElection&&state.listElection.data}/>
    </div>
  )
}

export default ContentDashboard