import React, { useState } from 'react'
import Cookies from 'js-cookie'
import { Link } from 'react-router-dom'
import { useMediaQuery } from 'react-responsive'
import FormElection from '../../Election/components/FormElection'
import Loading from '../../../utils/Loading'
import { TOKEN } from '../../../utils/constants'
import moment from 'moment'
import Footer from '../../components/Footer'
import { ElectionAPI } from '../../Election/api/ElectionAPI'
import { ShowNotify, notifyPosition, notifyType } from '../../../utils/notification'


const listElection = require("../../../assets/mock/mock")

const noneListImg = require("../../../assets/img/icons/report.svg")

function ListElection(props) {
  const isTablet = useMediaQuery({ query: '(max-width: 725px)' })
  const [state, setState] = useState({
    loading: false,
    idElection: '',
    dataEdit: null
  })
  // console.log(state.idElection)
  const delElection = async (unique_id, ballot_id) => {
    setState({ ...state, loading: true })
    const response = await ElectionAPI.delElection(unique_id)
    if (response) {
      if (response.status === 500) {
        ShowNotify("Network error", notifyPosition.topCenter, notifyType.error)
      } else if (response.status_code === 401) {
        ShowNotify("Invalid Token.", notifyPosition.topCenter, notifyType.error)
      } else {
        ShowNotify("Succesfully delete election", notifyPosition.topCenter, notifyType.success, () => {
          window.location.reload()
        })
      }
    }
    setState({ ...state, loading: false })
  }
  if (!props.listElection) {
    return <Loading />
  } else {
    if (props.listElection.status_code === 401) {
      Cookies.remove(TOKEN)
      return <div></div>
    }
    else {
      return (
        <div>
          <div className="title-text p-3 bg-white shadow">Dashboard</div>
          <div className={isTablet ? "list-election card card-body m-2" : "list-election card card-body m-5"}>
            <div className={props.listElection.length === 0 ? "d-none" : isTablet ? "d-block" : "d-flex"}>
              <div className="mr-auto">
                <button className="btn btn-success my-2 my-sm-0" data-toggle="modal" data-target="#addElectionModal">Add Election</button>
              </div>
              <div className={isTablet ? "mt-2" : "mr-2"}>
                <select className="custom-select" id="inputGroupSelect01" defaultValue="">
                  <option value="">Filter by Status</option>
                  <option value="Prepared">Prepared</option>
                  <option value="Complete">Complete</option>
                  <option value="Close">Close</option>
                  <option value="Running">Running</option>
                </select>
              </div>
              <form className={isTablet ? "mt-2 form-inline" : "ml-2 form-inline"}>
                <input className={isTablet ? "form-control w-100 mb-1" : "form-control mr-sm-2"} type="search" placeholder="Search" aria-label="Search" />
                <button className="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
              </form>
            </div>
            {props.listElection.length === 0 ? (
              <div className="d-flex align-items-center justify-content-center" style={{ height: "80vh" }}>
                <div className="text-center">
                  <img src={noneListImg} width="100" height="100" />
                  <br /><br /><p className="note-text">Click button below to add an election</p>
                  <button className="btn btn-success my-2 my-sm-0" data-toggle="modal" data-target="#addElectionModal">Add Election</button>
                </div>
              </div>
            ) :
              (
                <div className="pt-3 table-hover table-responsive">
                  <table className="table">
                    <thead className="table-dark">
                      <tr>
                        <th scope="col">No</th>
                        <th scope="col">Title</th>
                        <th scope="col">Start Date</th>
                        <th scope="col">End Date</th>
                        <th scope="col">Status</th>
                        <th className="text-center" scope="col">Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      {props.listElection.map((row, index) => (
                        <tr key={index}>
                          <th scope="row">{index + 1}</th>
                          <td>{row.title}</td>
                          <td>{moment(row.start_date).format("DD-MM-YYYY, HH:mm")}</td>
                          <td>{moment(row.end_date).format("DD-MM-YYYY, HH:mm")}</td>
                          <td>
                            <p className={row.status === "Complete" ? "badge badge-success" : row.status === "Running" ? "badge badge-info" : row.status === "Closed" ? "badge badge-danger" : row.status === "Prepared" && "badge badge-secondary"}>
                              {row.status}
                            </p>
                          </td>
                          <td className="text-center">
                            <Link to={`/election/${row.unique_id}`}><button className="btn btn-primary btn-sm m-1">Detail</button></Link>
                            <button className="btn btn-warning btn-sm m-1" data-toggle="modal" data-target={`#editElectionModal${row.unique_id}`} onClick={e => setState({ ...state, dataEdit: row, idElection: row.unique_id })}>Edit</button>
                            <button className="btn btn-danger btn-sm m-1" data-toggle="modal" data-target="#delElectionModal" onClick={e => setState({ ...state, idElection: row.unique_id })}>Hapus</button>
                            <div className="modal fade" id={`editElectionModal${row.unique_id}`} tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                              <div className="modal-dialog" role="document">
                                <div className="modal-content">
                                  <div className="modal-header">
                                    <h5 className="modal-title" id="exampleModalLabel">Edit Election</h5>
                                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                      <span aria-hidden="true">&times;</span>
                                    </button>
                                  </div>
                                  <div className="modal-body">
                                    <FormElection mode="edit" dataEdit={row} id={row.unique_id} />
                                  </div>
                                </div>
                              </div>
                            </div>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                  <nav aria-label="...">
                    <ul className="pagination">
                      <li className="page-item disabled">
                        <a className="page-link" href="#" tabIndex="-1">Previous</a>
                      </li>
                      <li className="page-item"><a className="page-link" href="#">1</a></li>
                      <li className="page-item active">
                        <a className="page-link" href="#">2 <span className="sr-only">(current)</span></a>
                      </li>
                      <li className="page-item"><a className="page-link" href="#">3</a></li>
                      <li className="page-item">
                        <a className="page-link" href="#">Next</a>
                      </li>
                    </ul>
                  </nav>
                </div>
              )}
            {!props.listElection && <Loading />}
          </div>
          <div className="modal fade" id="addElectionModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLabel">Add Election</h5>
                  <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div className="modal-body">
                  <FormElection mode="add" />
                </div>
              </div>
            </div>
          </div>

          <div className="modal fade" id="delElectionModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-body">
                  <div className="text-center">
                    <div className="subtitle-text">Hapus Election</div>
                    <div className="note-text">Apakah Anda ingin menghapus election ini? </div>
                    <div className="btn btn-group">
                      <div className="orange-btn" onClick={e => delElection(state.idElection)}>Ya, Hapus</div>
                      <div className="light-btn" data-dismiss="modal" aria-label="Close">Batal</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Footer />
        </div>
      )
    }
  }
}

export default ListElection